<template>
  <div class="one">
    <div>
      <canvas
        @click="clickWeakPoint"
        @mousemove="getMousePos($event)"
        id="canvas"
        :width="width"
        :height="height"
      ></canvas>
    </div>
  </div>
</template>

<script>
export default {
  name: "One",
  data: () => {
    return {
      width: window.innerWidth,
      height: 100,
      mouse_x: null,
      mouse_y: null,
    };
  },
  computed: {},
  watch: {
    mouse_x: function(newValue, oldValue) {
      if (newValue > 960 && newValue < 970) {
        this.drawWeakPoint();
      } else {
        this.clearWeakPoint();
      }
    }
  },
  methods: {
    clickWeakPoint() {
      if (this.mouse_x > 960 && this.mouse_x < 970) {
        this.$store.commit("eventNumIncrement");
      }
    },
    clearWeakPoint() {
      let canvas = document.getElementById("canvas");
      let ctx = canvas.getContext("2d");
      ctx.clearRect(960, 0, 10, 100);
      ctx.beginPath();
      ctx.strokeStyle = "#FFFFFF";
      ctx.strokeRect(960, 0, 10, 100);
    },
    drawWeakPoint() {
      let canvas = document.getElementById("canvas");
      let ctx = canvas.getContext("2d");
      ctx.beginPath();
      ctx.strokeStyle = "#e00d29";
      ctx.strokeRect(960, 0, 10, 100);
    },
    getMousePos(event) {
      let canvas = document.getElementById("canvas");
      let rect = canvas.getBoundingClientRect();
      this.mouse_x = event.clientX - rect.left;
      this.mouse_y = event.clientY - rect.left;
      console.log({
        x: event.clientX - rect.left,
        y: event.clientY - rect.top
      });
    },
    draw() {
      let canvas = document.getElementById("canvas");
      let ctx = canvas.getContext("2d");
      let x = -50;
      let y = 0;
      ctx.beginPath();
      ctx.strokeStyle = "#FFFFFF";
      let interval = setInterval(() => {
        x++;
        ctx.strokeRect(x * 10, y * Math.random() * 5, 150, 100);
        if (x > 1000) {
          clearInterval(interval);
        }
      }, 20);
    }
  },
  mounted() {
    this.draw();
  }
};
</script>

