<template>
  <div class="main-box">
    <div v-if="current_event_num == 0">
      <div class="text-box">
        <vue-typed-js
          :strings="['The fence stretched to the horizon in both directions...']"
          :showCursor="false"
          :typeSpeed="35"
          @onComplete="next(1000)"
        >
          <h1 class="typing"></h1>
        </vue-typed-js>
      </div>
    </div>
    <div v-else-if="current_event_num == 2">
      <div class="text-box">
        <vue-typed-js
          :strings="['A loose panel is pried away from the fence, leaving just enough room to creep through. In the distance, the burnt out remains of a city.']"
          :showCursor="false"
          :typeSpeed="35"
          @onComplete="next(2000)"
        >
          <h1 class="typing"></h1>
        </vue-typed-js>
      </div>
    </div>
    <div v-else-if="current_event_num == 4">
      <div class="text-box">
        <vue-typed-js
          :strings="['Somewhere in the city is the control panel...']"
          :showCursor="false"
          :typeSpeed="35"
        >
          <h1 class="typing"></h1>
        </vue-typed-js>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TextArea",
  data: () => {
    return {
      current_event_num: null
    };
  },
  methods: {
    next(timeout) {
      setTimeout(() => {
        console.log("next");
        this.$store.commit("eventNumIncrement");
      }, timeout);
    }
  },
  computed: {
    eventNum() {
      this.current_event_num = this.$store.state.event_num;
      return this.$store.state.event_num;
    }
  },
  watch: {
    eventNum(newValue, oldValue) {
      this.current_event_num = newValue;
    }
  }
};
</script>

