<template>
  <div class="main-box">
    <One v-if="current_event_num == 1"/>
    <Two v-else-if="current_event_num == 3"/>
  </div>
</template>

<script>
import One from "@/components/levels/One.vue";
import Two from "@/components/levels/Two.vue";
export default {
  name: "PlayArea",
  components: {
    One,
    Two
  },
  props: {},
  data: () => {
    return {
      current_event_num: null
    };
  },
  computed: {
    eventNum() {
      this.current_event_num = this.$store.state.event_num;
      return this.$store.state.event_num;
    }
  },
  watch: {
    eventNum(newValue, oldValue) {
      this.current_event_num = newValue;
    }
  }
};
</script>

