<template>
  <div class="two">
    <div>
      <canvas @click="click()" @mousemove="getMousePos($event)" id="canvas" :width="width" :height="height"></canvas>
    </div>
  </div>
</template>

<script>
import { tween } from "femtotween";

export default {
  name: "Two",
  data: () => {
    return {
      width: window.innerWidth,
      height: 600,
      mouse_x: null,
      mouse_y: null
    };
  },
  computed: {},
  watch: {},
  methods: {
    click() {
      this.$store.commit("eventNumIncrement");
    },
    getMousePos(event) {
      let canvas = document.getElementById("canvas");
      let rect = canvas.getBoundingClientRect();
      this.mouse_x = event.clientX - rect.left;
      this.mouse_y = event.clientY - rect.left;
      console.log({
        x: event.clientX - rect.left,
        y: event.clientY - rect.top
      });
    },
    getRandomInt(min, max) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min + 1)) + min;
    },
    drawBuilding(ctx, fill, fillColour, borderColour, x, y, w, h) {
      ctx.strokeStyle = borderColour;
      ctx.fillStyle = fillColour;
      if (fill) {
        ctx.fillRect(x, y, w, h);
      } else {
        ctx.strokeRect(x, y, w, h);
      }
    },
    ambience() {
      let canvas = document.getElementById("canvas");
      let ctx = canvas.getContext("2d");
      ctx.fillStyle = "#6b1a55";
      let smoke = (delay, x_, y_) => {
        let x = x_;
        let y = y_;
        let w = 1;
        let h = 1;
        setTimeout(() => {
          tween(
            50,
            -5,
            v => {
              ctx.clearRect(x - 1, y - 2, w + 5, h + 5);
              y = Math.round(v);
              x += Math.random();
              ctx.fillRect(x, y, w, h);
              ctx.fillRect(x + this.getRandomInt(0, 4), y + this.getRandomInt(0, 2), w, h);
              ctx.fillRect(x + this.getRandomInt(0, 4), y + this.getRandomInt(0, 2), w, h);
              ctx.fillRect(x + this.getRandomInt(0, 4), y + this.getRandomInt(0, 2), w, h);
              ctx.fillRect(x + this.getRandomInt(0, 4), y + this.getRandomInt(0, 2), w, h);
              
            },
            {
              time: this.getRandomInt(3,6) * 1000,
              done: () => {
                x = canvas.width / 2;
                y = -100;
                w = 2;
                h = 2;
                smoke(delay, x_, y_);
              }
            }
          );
        }, delay);
      };
      smoke(0, canvas.width / 2, -100);
      smoke(100, canvas.width / 2, -100);
      smoke(400, canvas.width / 2 + 50, -100);
      smoke(800, canvas.width / 2 + 25, -100);
      smoke(200, canvas.width / 2 + 25, -100);
      smoke(600, canvas.width / 2 + 10, -100);
      smoke(300, canvas.width / 2 + 10, -100);
    },
    draw() {
      let canvas = document.getElementById("canvas");
      let ctx = canvas.getContext("2d");
      let x = canvas.width / 2;
      let y = 100;
      let w = 100;
      let h = 200;
      ctx.beginPath();
      this.drawBuilding(ctx, false, "#FFFFFF", "#FFFFFF", x, y, w, h);
      this.drawBuilding(
        ctx,
        false,
        "#FFFFFF",
        "#FFFFFF",
        x - 30,
        y - 30,
        w,
        h + 30
      );
      this.drawBuilding(
        ctx,
        false,
        "#FFFFFF",
        "#FFFFFF",
        x - 105,
        y - 60,
        w - 10,
        h + 60
      );
      this.drawBuilding(
        ctx,
        true,
        "#FFFFFF",
        "#FFFFFF",
        x - 60,
        y - 20,
        w - 30,
        h + 20
      );
      this.drawBuilding(
        ctx,
        false,
        "#FFFFFF",
        "#FFFFFF",
        x - 185,
        y - 25,
        w - 40,
        h + 25
      );
      this.drawBuilding(
        ctx,
        true,
        "#FFFFFF",
        "#FFFFFF",
        x - 180,
        y + 20,
        w - 5,
        h - 20
      );
      this.drawBuilding(
        ctx,
        true,
        "#FFFFFF",
        "#FFFFFF",
        x + 60,
        y + 20,
        w - 5,
        h - 20
      );
    }
  },
  mounted() {
    this.ambience();
    this.draw();
  }
};
</script>

